<template>
    <van-popup class="uploadPopup" v-model="show" position="bottom" :overlay=false @open='onPlayerPageOpened' :close-on-popstate=true get-container="body">
      <top-nav-bar :title="title" @close="onclose" :popup="true"/>
      <div class="listenWrapper">
         <van-cell value=""/>
         <van-cell>
            <van-row class="listen" type="flex" justify="center" align="center">
              <van-col span="1" @click="togglePlay"><span class="iconfont" :class="[playing?'icon-pause':'icon-play']"/></van-col>
              <van-col span="4" >{{timer|s_to_hs}}</van-col>
              <van-col span="15" ><van-slider v-model="timer" active-color="#1989fa" inactive-color="#ddd" :max='duration' :disabled='sliderDisabled' @change='seek'/></van-col>
              <van-col span="4">{{duration|s_to_hs}}</van-col>
            </van-row>
        </van-cell>
      </div>
      <van-form ref="form" @submit="onSubmit">
          <div class="fieldsWrapper">
            <van-field
              v-model.trim="recordSound.name"
              name="title"
              label=""
              placeholder=""
              input-align="right"
              :rules="[{ required: true, message: '' }]"
            />
            <van-field name="commenton" label="" input-align="right">
              <template #input>
                <van-switch v-model="commentOn" size="20"/>
              </template>
            </van-field>
            <van-field name="publicon" label="" input-align="right">
              <template #input>
                 <van-switch v-model="publicOn" size="20"/>
              </template>
            </van-field>
            <van-cell class="remarkLabel" value="" />
            <van-field class="remark" type="textarea" name="remark" v-model.trim="remark" rows="1" placeholder="" maxlength="40" show-word-limit/>
            <input type="hidden" :value="draft"/>
          </div>
          <div class="buttonWrapper">
              <van-row>
                <van-col span="11"><van-button block plain :loading=draftSubmitting type="info" loading-text="..."  @click="onSveRecordDraft" :disabled="submitDisabled" native-type="button"></van-button></van-col>
                <van-col span="11" offset="2"><van-button block :loading=publishSubmitting type="info" loading-text="..." @click="onSveRecord"  :disabled="submitDisabled" native-type="button"></van-button></van-col>
              </van-row>
              <div class="agreement"><span @click="showRightsDialog"></span><span  @click="showPublicDialog"></span></div>
          </div>
      </van-form>
    </van-popup>
</template>

<script>
import '@/styles/fonts/iconfont.css' // global css
import {saveRecord} from '@/api/reading'
import { mapGetters } from 'vuex'
import { Col, Row, Cell, Button, Popup, Dialog, Slider, Form, Field, Switch , Toast } from 'vant';
import topNavBar from '@/components/TopNavBar'
import {Howl} from 'howler';
export default {
  name:"uploadPopup",
  components: {topNavBar,[Col.name]:Col, [Row.name]:Row, [Cell.name]:Cell ,[Button.name]:Button, [Popup.name]:Popup, [Slider.name]:Slider,[Form.name]:Form,[Field.name]:Field , [Switch.name]:Switch, [Toast.name]:Toast, [Dialog.name]:Dialog},
  props: {
    show: {
      type: Boolean,
      default:false
    },
    recordSound: {
      type: Object,
      default: () => {return { name:'',createTime:'', resourceId:'',duration:0, sound:null,uploaded:false}}
    },
  },
  data() {
    return {
      title:"",
      commentOn:true,
      publicOn:true,
      remark:'',
      timer:0,
      duration:0,
      soundHowl: null,
      playing:false,
      draft:false,
      draftSubmitting:false,
      publishSubmitting:false,
      lastSaveTime:null,
      lastSaveDraftTime:null,
      lastSaveFormValue:null,
      lastSaveDraftFormValue:null
    }
  },
  mounted(){
    Toast.setDefaultOptions({ duration: 3000 });
  },
  methods:{
    onSubmit(values){
      //TODO
      if(!this.formModified(values)){
        Toast("")
        if(this.draft){
            this.draftSubmitting = false
        }else{
            this.publishSubmitting = false
        }
        return
      }
      if(this.recordSound.duration==0){
        Toast("")
        return
      }
      let formData = new FormData()
      formData.append("id", this.recordSound.resourceId)
      formData.append("title", values["title"])
      formData.append("commenton", values["commenton"])
      formData.append("publicon", values["publicon"])
      formData.append("remark", values["remark"])
      formData.append("draft", this.draft)
      //formData.append("wish", values["wish"]);
      formData.append("sound", this.recordSound.sound)
      formData.append("recordtime", this.recordSound.recordTime)
      formData.append("format", 'mp3')

      let that = this
      saveRecord(formData).then(response => {
        const {message } = response
        Toast(message)
        if(that.draft){
          that.draftSubmitting = false
          that.lastSaveDraftFormValue = values
          that.lastSaveDraftTime = that.recordSound.saveTime
        }else{
          that.publishSubmitting = false
          that.lastSaveFormValue = values
          that.lastSaveTime = that.recordSound.saveTime
        }
       
        this.$emit("upload",true)
      }).catch(error => {
          Toast(error);
          if(that.draft){
            that.draftSubmitting = false
          }else{
            that.publishSubmitting = false
          }
      })
    },
    formModified(formValues){
        let lastSaveTime = this.draft ? this.lastSaveDraftTime:this.lastSaveTime
        let lastValues = this.draft ? this.lastSaveDraftFormValue:this.lastSaveFormValue
        if(lastSaveTime != this.recordSound.saveTime){
          return true
        }
        for(let key in formValues){
          if(null == lastValues || (Object.prototype.hasOwnProperty.call(lastValues, key) && lastValues[key]!==formValues[key])){
              return true
          }
        }
        return false
    },
    onSveRecordDraft(){
      this.draft = true
      this.draftSubmitting = true
      this.$refs.form.submit()
    },
    onSveRecord(){
      this.draft = false
      this.publishSubmitting = true
      this.$refs.form.submit()
    },
    onPlayerPageOpened(){
      if(!this.recordSound.sound){
        Toast("")
        this.$emit('close')
      }else if(this.recordSound.duration != this.duration){
        //
        this.soundHowl = this.createSoundHowl()
        this.duration = this.recordSound.duration
      }
    },
    togglePlay(){
      if(!this.soundHowl){return}
      this.playing ? this.pause() : this.play()
    },
    play(){
      if(this.soundHowl){
        this.soundHowl.play()
      }
    },
    pause(){
      if(this.soundHowl){
        this.soundHowl.pause()
      }
    },
    seek(position){
        this.timer = position
        this.soundHowl.seek( this.timer)
    },
    createSoundHowl(){
      if(!this.recordSound.sound){return null}
      let that = this
      let url =  (window.URL||window.webkitURL).createObjectURL(this.recordSound.sound)
      return  new Howl({
        src: url,
        format:"mp3",
        onload (){
          that.duration = Math.round(that.soundHowl.duration())
          that.recordSound.duration = that.duration
        },
        onplay(){
          that.playing = true
          requestAnimationFrame(that.continuePosition.bind(that))
        },
        onpause(){
          that.playing = false
        },
        onend(){
          that.playing = false
          that.soundHowl.stop()
        },
        onloaderror (error){
             console.log("load error:"+error)
        },
        onplayerror(error){
             console.log("play error:"+error)
        }
       })
    },
    continuePosition() {
       //slider
       this.timer = Math.round(this.soundHowl.seek()) || 0;
       if (this.playing) {
          requestAnimationFrame(this.continuePosition.bind(this));
       }
    },
    showRightsDialog(){
        Dialog.alert({
          title: "",
          message:"44.1 (TRIPS)TRIPS"
          })
    },
    showPublicDialog(){
        Dialog.alert({
          title: "",
          message:"44.1 (TRIPS)TRIPS"
        })
    },
    onclose(){
      this.pause()
      this.$emit('close')
    }
  },
  computed:{
    ...mapGetters({
      getRecordByTime: 'recorder/getRecordByTime'
    }),
    sliderDisabled(){
      return false
    },
    submitDisabled(){
      if( this.draftSubmitting || this.publishSubmitting){
        return true
      }
      return false
    }
  }
}
</script>
<style lang="scss" scoped>
.uploadPopup{
  height:100%;
  background-color:#eee;
}
.listen{
  text-align: center;
}
::v-deep .van-slider__button{
  width: 18px;
  height: 18px;
}
::v-deep .van-field__label{
  color:#323233;
}
::v-deep .remarkLabel{
  padding-bottom:0;
} 
::v-deep .remark .van-cell__value {
  border: 1px solid #ebedf0;
  padding:20px;
}
::v-deep .remarkLabel, ::v-deep .remark{
  &:after{
    border:none
  }
}
.listenWrapper,.fieldsWrapper{
  padding:40px 20px;
  background: #fff;
  margin-bottom:30px;
}
.buttonWrapper{
  padding:10px 60px;
}
.agreement{
  color:#999;
  font-size:24px;
  text-align: center;
  margin-top:40px;
  span{
    color:#1989fa;
    font-weight:bold;
  }
}
</style>