export default class RecordSound {
  constructor({ resourceId, recordTime, saveTime, name, duration, blob}) {
    this.resourceId = resourceId
    this.recordTime = recordTime
    this.saveTime = saveTime
    this.name = name              //
    this.duration = duration || 0  //
    this.sound = blob
  }
}

export function createRecordSound(recordData) {
  return new RecordSound(recordData)
}