/*
 RecordAppios

JsSDKapi

IOSWKWebView(UIWebView)getUserMediaH5AndroidIOS

app.jsIOS-Weixin.Configapp-support-sample

https://github.com/xiangyuecn/Recorder
*/
(function(){
"use strict";

var App=RecordApp;
var CLog=App.CLog;
var platform=App.Platforms.Weixin;
var config=platform.Config;

platform.IsInit=true;

var WXRecordData={};

/*******IOS-Weixin*******/

platform.RequestPermission=function(success,fail){
	if(permissionState==5){//
		success();
		return;
	};
	permissionCalls.push({t:success,f:fail});
	if(permissionState==1){
		CLog("RequestPermissionwx",3);
		return;
	};
	permissionState=1;
	
	var end=function(err,isUser){
		permissionState=err?0:5;
		var arr=permissionCalls;
		permissionCalls=[];
		for(var i=0;i<arr.length;i++){
			if(err){
				arr[i].f(err,isUser);
			}else{
				arr[i].t();
			};
		};
	};
	
	config.WxReady(function(wx,err){
		WXRecordData.wx=null;
		if(err){
			end("JsSDK"+err);
			return;
		};
		WXRecordData.wx=wx;
		
		//start
		killStart(function(){
			// 
			wx.startRecord({
				success:function(){
					//
					var kill=function(wait,Final){
						setTimeout(function(){
							stopNow(function(err){
								if(err){
									CLog("wx"+(Final?'[Final]':'')+":"+err,3);
								};
								if(!err || Final){
									end();
								}else{
									kill(1000,1);//
								};
							});
						},wait);
					};
					kill(100);
				}
				,fail:function(o){
					end(""+o.errMsg);
				}
				,cancel:function(o){
					end(""+o.errMsg,true);
				}
			});
		});
	});
};
var permissionState;//0  1 5
var permissionCalls=[];

var isWaitStart,isStart,startID=0;
var stopNow=function(call,keep){
	if(!keep){
		isStart=0;
		isWaitStart=0;
	};
	WXRecordData.wx.stopRecord({
		success:function(){
			call&&call();
		},fail:function(o){
			call&&call(""+o.errMsg);
		}
	});
};
var killStart=function(call,keep){
	if(!keep && isStart){
		CLog("kill",3);
	};
	stopNow(function(){
		setTimeout(call,300);
	},keep);
};
platform.Start=function(set,success,fail){
	var wx=WXRecordData.wx;
	if(!wx){
		fail("RequestPermission");
		return;
	};
	if(isStart){
		CLog("wxkill",3);
		killStart(function(){
			platform.Start(set,success,fail);
		});
		return;
	};
	if(isWaitStart){
		CLog("wxStart...",3);
		setTimeout(function(){
			platform.Start(set,success,fail);
		},600);
		return;
	};
	var id=++startID;//id
	
	var startFail=function(o){
		isWaitStart=0;
		fail(""+o.errMsg);
		stopNow();
	};
	
	//
	var run=function(Final){
		isStart=0;
		isWaitStart=1;
		wx.startRecord({
			success:function(){
				isStart=1;
				isWaitStart=0;
				WXRecordData.startTime=Date.now();
				WXRecordData.start=set;
				CLog("wx");
				success();
			}
			,fail:function(e){
				if(Final){
					startFail(e);
				}else{
					killStart(function(){//
						run(1);
					});
				};
			}
			,cancel:startFail
		});
	};
	run();
	
	//
	WXRecordData.chunks=[];
	WXRecordData.chunkErr="";
	stopJoinEnd=null;
	stopWaitChunk=null;
	wx.onVoiceRecordEnd({
		complete:function(res){
			var t1=Date.now();
			if(stopJoinEnd){
				//stoponVoiceRecordEndstop
				stopJoinEnd(res,"chunk");
			}else{
				if(res.localId && WXRecordData.chunks){
					WXRecordData.chunks.push({res:res,duration:t1-WXRecordData.startTime,time:t1,from:"chunk"});
				}else{
					//
					CLog("wx chunk",3,res);
				};
			};
			
			if(stopWaitChunk){//
				stopWaitChunk();
				return;
			};
			
			CLog("wx...");
			var rerun=function(tryCount){
				if(!isStart || id!=startID){
					CLog("wx",3);
					return;
				};
				wx.startRecord({
					success:function(){
						if(id==startID){//
							WXRecordData.startTime=Date.now();
							CLog("wx,"+(Date.now()-t1)+"ms");
						}
					}
					,fail:function(o){
						if(!isStart || id!=startID){
							return;
						};
						var msg=""+o.errMsg;
						CLog(msg,1,o);
						if(tryCount>2){
							WXRecordData.chunkErr=msg;
						}else{
							tryCount++;
							CLog("..."+tryCount);
							killStart(function(){//
								rerun(tryCount);
							},1);
						};
					}
				});
			};
			rerun(0);
		}
	});
};

var stopJoinEnd,stopWaitChunk;
platform.Stop=function(successx,failx){
	var wx=WXRecordData.wx;
	isStart=0;
	var hasWaitChunk=!!stopWaitChunk;//59
	stopWaitChunk=null;
	CLog("");
	
	var failCall=function(msg){
		failx("[wx]"+(msg.errMsg||msg));
	};
	var set=WXRecordData.start;
	if(!set){
		failCall("");
		return;
	};
	//1
	var curDuration=Date.now()-WXRecordData.startTime;
	if(!hasWaitChunk && curDuration>59100){
		CLog("wx1stop...",3);
		stopWaitChunk=function(){
			platform.Stop(successx,failx);
		};
		return;
	};
	
	WXRecordData.start=null;
	var dwxData={list:[]};
	set.DownWxMediaData=dwxData;
	
	// amr
	var transform=function(){
		var list=dwxData.list;
		var list0=list[0];
		if(list0.duration){
			//
			var bstr=atob(list0.data),n=bstr.length,u8arr=new Uint8Array(n);
			while(n--){
				u8arr[n]=bstr.charCodeAt(n);
			};
			var blob=new Blob([u8arr.buffer], {type:list0.mime});
			
			App._SRec=null;
			CLog("RecordApp.GetStopUsedRec",3);
			successx(blob,list0.duration);
			return;
		};
		
		var pcms=[];
		var enTime=0;
		var encode=function(){
			enTime||(enTime=Date.now());
			var pcm=[];
			
			//copyRecorder.SampleRaise(pcms,8000,set.sampleRate)
			var step=set.sampleRate/8000;
			if(step<=1){
				step=1;
			}else{
				CLog("arm8000hz"+set.sampleRate+"hz",3);
			};
			var posFloat=0,prev=0;
			for(var i=0;i<pcms.length;i++){
				var o=pcms[i];
				for(var i2=0;i2<o.length;i2++){
					var cur=o[i2];
					
					var pos=Math.floor(posFloat);
					posFloat+=step;
					var end=Math.floor(posFloat);
					
					//prevcur
					var n=(cur-prev)/(end-pos);
					for(var j=1;pos<end;pos++,j++){
						pcm.push( Math.floor(prev+(j*n)) );
					};
					
					prev=cur;
				};
			};
			
			var rec=Recorder(set).mock(pcm,set.sampleRate);
			rec.stop(function(blob,duration){
				dwxData.encodeTime=Date.now()-enTime;
				
				//
				for(var k in rec.set){
					set[k]=rec.set[k];
				};
				App._SRec=rec;
				successx(blob,duration);
			},failCall);
		};
		
		var deidx=0;
		var deTime=0;
		var decode=function(){
			deTime||(deTime=Date.now());
			if(deidx>=list.length){
				dwxData.decodeTime=Date.now()-deTime;
				encode();
				return;
			};
			
			var data=list[deidx];
			data.duration=chunkList[deidx].duration;
			data.isAmr=true;
			var bstr=atob(data.data),n=bstr.length,u8arr=new Uint8Array(n);
			while(n--){
				u8arr[n]=bstr.charCodeAt(n);
			};
			
			Recorder.AMR.decode(u8arr,function(pcm){
				pcms.push(pcm);
				deidx++;
				decode();
			},function(msg){
				failCall("AMR"+(deidx+1)+":"+msg);
			});
		};
		
		if(Recorder.AMR){
			decode();
		}else{
			failCall("AMR");
		};
	};
	
	
	//
	var mediaIds=[];
	var stopFn=function(){
		if(!successx){//
			failCall("");
			return;
		};
		
		var upIds=[];
		for(var i=0;i<chunkList.length;i++){
			upIds.push(chunkList[i].res.localId);
		};
		CLog(""+upIds.length+"",upIds,chunkList);
		if(!upIds.length){
			failCall("");
			return;
		};
		
		//
		var downidx=0;
		var downStart=0;
		var downEnd=function(){
			dwxData.downTime=Date.now()-downStart;
			//
			CLog("...");
			transform();
		};
		var down=function(tryCount){
			downStart||(downStart=Date.now());
			if(downidx>=mediaIds.length){
				downEnd();
				return;
			};
			var serverId=mediaIds[downidx];
			
			config.DownWxMedia({
				mediaId:serverId
				,transform_mediaIds:mediaIds.join(",")
				,transform_type:set.type
				,transform_bitRate:set.bitRate
				,transform_sampleRate:set.sampleRate
			},function(data){
				dwxData.list.push(data);
				//
				if(data.duration){
					downEnd();
					return;
				};
				
				if(/amr/i.test(data.mime)){
					downidx++;
					down();
				}else{
					failCall(""+data.mime);
				};
			},function(msg){
				tryCount=tryCount||0;
				if(tryCount>2){
					failCall(""+msg);
				}else{
					tryCount++;
					CLog("DownWxMedia..."+tryCount,1,msg);
					down(tryCount);
				};
			});
		};
		
		
		//
		var upidx=0;
		var up=function(tryCount){
			if(upidx>=upIds.length){
				dwxData.uploadTime=Date.now()-upStart;
				CLog("...");
				down();
				return;
			};
			var localId=upIds[upidx];
			CLog("wx["+upidx+"] wx.playVoice({localId:'"+localId+"'})");
			wx.uploadVoice({
				localId:localId
				,isShowProgressTips:0
				,fail:function(e){
					tryCount=tryCount||0;
					if(tryCount>2){
						failCall("uploadVoice["+upidx+"]"+e.errMsg);
					}else{
						tryCount++;
						CLog("uploadVoice..."+tryCount,1,e);
						up(tryCount);
					};
				}
				,success:function(res){
					var serverId=res.serverId;
					CLog("OK serverId:"+serverId);
					
					mediaIds.push(serverId);
					upidx++;
					up();
				}
			});
		};
		var upStart=Date.now();
		up();
	};
	
	
	
	
	
	var chunkList=WXRecordData.chunks;
	if(WXRecordData.chunkErr){// 
		CLog(WXRecordData.chunkErr,1,chunkList);
		failCall(""+chunkList.length+""+WXRecordData.chunkErr);
		return;
	};
	if(hasWaitChunk){//59
		stopFn();
		return;
	};
	if(chunkList.length){
		if(Date.now()-chunkList[chunkList.length-1].time<900){
			CLog("",3);
			stopNow();
			stopFn();
			return;
		};
	};
	
	//onVoiceRecordEndonVoiceRecordEndstop1
	stopJoinEnd=function(res,from){
		stopJoinEnd=null;
		
		var t1=Date.now();
		if(res.localId){
			chunkList.push({res:res,duration:t1-WXRecordData.startTime,time:t1,from:from});
		}else{
			//nlocalId
			CLog(""+from+"",3,res);
		};
		WXRecordData.chunks=null;//
		
		stopFn();
	};
	wx.stopRecord({
		fail:function(e){
			stopJoinEnd=null;
			//
			if(chunkList.length && curDuration<3000){
				CLog(""+e.errMsg,3);
				stopFn();
			}else{
				failCall(e);
			};
		}
		,success:function(res){
			stopJoinEnd&&stopJoinEnd(res,"stop");
		}
	});
};
})();