config.DownWxMedia=function(param,success,fail){
	/* https://mp.weixin.qq.com/wiki?t=resource/res_main&id=mp1444738727
	param:{//
		mediaId"" IDDownWxMedia
		
		transform_mediaIds:"mediaId,mediaId,mediaId" 1mediaId
		transform_type:"mp3" setmimeaudio/type(audio/mp3)
		transform_bitRate:123 transform_type
		transform_sampleRate:123 transform_type
		
		* amrspeextransform_*amrmediaId
	}
	success fn(obj) 
		obj:{
			mime:"audio/amr" //Content-Typeaudio/amrmimeduration
			,data:"base64" //base64
			
			,duration:0 //>00
		}
	fail: fn(msg) 
	*/
	
	var failCall=function(err){
		CLog(err,1);
		fail(err);
	};
	
	CLog("...",param);
	ajax(MyWxApi,{
		action:"wxdown"
		,mediaID:param.mediaId
		,transform_mediaIds:param.transform_mediaIds
		,transform_type:param.transform_type
		,transform_bitRate:param.transform_bitRate
		,transform_sampleRate:param.transform_sampleRate
	},function(data,raw){
		var mime=data.mime,b64=data.data;
		if(!mime || !b64 || (b64.length||1)%4!=0 ){
			CLog("",1,raw);
			failCall("json");
		}else{
			CLog(""+mime+" "+(b64.length/4*3)+"b");
			success(data);
		}
	},function(msg){
		failCall(""+msg);
	});
};
